"use client";

import { useState, useRef, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Slider } from "@/components/ui/slider";
import { Download, UploadCloud } from "lucide-react";

export default function VocalRemoverClient() {
  const [originalAudio, setOriginalAudio] = useState<string | null>(null);
  const [instrumentalAudio, setInstrumentalAudio] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [volume, setVolume] = useState(1);

  const originalRef = useRef<HTMLAudioElement | null>(null);
  const instrumentalRef = useRef<HTMLAudioElement | null>(null);

  const handleUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;
    const url = URL.createObjectURL(file);
    setOriginalAudio(url);
    setInstrumentalAudio(null);
  };

  const simulateVocalRemoval = async () => {
    if (!originalAudio) return;
    setLoading(true);

    // Simulate vocal removal processing delay
    setTimeout(() => {
      setInstrumentalAudio(originalAudio); // In reality, you'd replace with the real processed audio
      setLoading(false);
    }, 3000);
  };

  const downloadAudio = (url: string, name: string) => {
    const link = document.createElement("a");
    link.href = url;
    link.download = name;
    link.click();
  };

  useEffect(() => {
    if (originalRef.current) originalRef.current.volume = volume;
    if (instrumentalRef.current) instrumentalRef.current.volume = volume;
  }, [volume]);

  return (
    <div className="max-w-3xl mx-auto p-6 space-y-6">
      <div className="flex flex-col items-center gap-4">
        <Input
          type="file"
          accept="audio/*"
          onChange={handleUpload}
          className="w-full cursor-pointer"
        />
        <Button
          onClick={simulateVocalRemoval}
          disabled={!originalAudio || loading}
          className="flex gap-2"
        >
          <UploadCloud className="w-4 h-4" />
          {loading ? "Processing..." : "Remove Vocals"}
        </Button>
        <Slider
          value={[volume]}
          onValueChange={(val) => setVolume(val[0])}
          max={1}
          step={0.01}
          className="w-64"
        />
      </div>

      {loading && (
        <div className="flex justify-center">
          <div className="w-8 h-8 border-4 border-t-transparent border-white rounded-full animate-spin" />
        </div>
      )}

      {originalAudio && (
        <div className="bg-gray-800 rounded-xl p-4 space-y-2">
          <h2 className="text-lg font-semibold">Original Audio</h2>
          <audio ref={originalRef} controls src={originalAudio} className="w-full" />
          <EqualizerBars />
          <Button onClick={() => downloadAudio(originalAudio, "original_audio.mp3")} className="mt-2 w-full">
            <Download className="w-4 h-4 mr-2" />
            Download Original
          </Button>
        </div>
      )}

      {instrumentalAudio && !loading && (
        <div className="bg-gray-900 rounded-xl p-4 space-y-2">
          <h2 className="text-lg font-semibold">Instrumental (No Vocals)</h2>
          <audio ref={instrumentalRef} controls src={instrumentalAudio} className="w-full" />
          <EqualizerBars />
          <Button onClick={() => downloadAudio(instrumentalAudio, "instrumental_audio.mp3")} className="mt-2 w-full">
            <Download className="w-4 h-4 mr-2" />
            Download Instrumental
          </Button>
        </div>
      )}
    </div>
  );
}

const EqualizerBars = () => (
  <div className="flex gap-1 items-end h-8 mt-2">
    {[...Array(5)].map((_, i) => (
      <div
        key={i}
        className={`w-1 bg-green-400 animate-pulse`}
        style={{ animationDelay: `${i * 0.1}s`, animationDuration: "1s", height: `${(i + 1) * 5}px` }}
      />
    ))}
  </div>
);
